'use client';

import { type HTMLAttributes } from 'react';
import { AlertCircle, CheckCircle2, Info, XCircle } from 'lucide-react';
import { cn } from '@/lib/utils';

export interface AlertProps extends HTMLAttributes<HTMLDivElement> {
  variant?: 'info' | 'success' | 'warning' | 'error';
  title?: string;
}

function Alert({ className, variant = 'info', title, children, ...props }: AlertProps) {
  const variants = {
    info: {
      container: 'bg-blue-50 dark:bg-blue-500/10 border-blue-200 dark:border-blue-500/20',
      icon: 'text-blue-500 dark:text-blue-400',
      title: 'text-blue-800 dark:text-blue-300',
      content: 'text-blue-700 dark:text-blue-400',
    },
    success: {
      container: 'bg-green-50 dark:bg-green-500/10 border-green-200 dark:border-green-500/20',
      icon: 'text-green-500 dark:text-green-400',
      title: 'text-green-800 dark:text-green-300',
      content: 'text-green-700 dark:text-green-400',
    },
    warning: {
      container: 'bg-yellow-50 dark:bg-yellow-500/10 border-yellow-200 dark:border-yellow-500/20',
      icon: 'text-yellow-500 dark:text-yellow-400',
      title: 'text-yellow-800 dark:text-yellow-300',
      content: 'text-yellow-700 dark:text-yellow-400',
    },
    error: {
      container: 'bg-red-50 dark:bg-red-500/10 border-red-200 dark:border-red-500/20',
      icon: 'text-red-500 dark:text-red-400',
      title: 'text-red-800 dark:text-red-300',
      content: 'text-red-700 dark:text-red-400',
    },
  };

  const icons = {
    info: Info,
    success: CheckCircle2,
    warning: AlertCircle,
    error: XCircle,
  };

  const Icon = icons[variant];
  const styles = variants[variant];

  return (
    <div
      className={cn('flex gap-3 rounded-lg border p-4', styles.container, className)}
      role="alert"
      {...props}
    >
      <Icon className={cn('h-5 w-5 flex-shrink-0', styles.icon)} />
      <div>
        {title && <h4 className={cn('font-medium', styles.title)}>{title}</h4>}
        <div className={cn('text-sm', styles.content, title && 'mt-1')}>{children}</div>
      </div>
    </div>
  );
}

export { Alert };
